                        <math class="ms">H_2=\gamma_2^\infty f_2 </math>
                        <math class="ms em">\gamma_2^\ast =\gamma_2/\gamma_2^\infty</math>
                        <br>
                        <img src="../../../public/img/fund/b_c_henry.png" alt="" class="piclarge"><br>
