<script >
    import Math from "@/components/Math";
    import Vue from 'vue';
    export default Vue.extend({
        components: {Math},
        name: "",
        data: () => ({
            active: 'first',
            first: false,
            second: false,
            third: false,
            fourth: false,
            fifth: false,
            k: 0,
            r: 0,
            s1: false,
            s2: false,
            s3:false,
            s4:false,
            sk:'first',
            
        }),
        methods: {
            setDone(id, index) {
                this[id] = true;
                if (index) {
                    this.active = index
                }
            },
            judge:function(t){
                this[t]=1;
            },
            //@click="show('s1')" this[t]data(s1)data
            show:function(t){
               this[t]=true;
            },
            
            hide:function(t){
                this[t]=false;
            },
        },
        mounted: function () {
            var _this=this;
            var hash = window.location.hash;
            var index = hash.lastIndexOf("#");
            var sk=hash.lastIndexOf('#', index - 1);
            if ((index != -1) &&(sk >0)) {
            var id = hash.substring(index + 1, hash.length + 1);
            var act=hash.substring(sk+ 1, index);
            _this.active=act;
            var div = document.getElementById(id);
            if (div) {
                this.$nextTick(() => {
                div.scrollIntoView();
                });
            }
            }
        },
    })
 </script>

